<template>
    <div class="login-container">
        <p class="login-title">서비스 이용약관 동의</p>
        <div class="button-checkbox">
            <v-btn type="button" rounded depressed class="whiteButton" @click="allCheck()">
                <label class="custome-checked-box">
                    <div :class="'out-line '+checkClass">
                        <font-awesome-icon class="checked-icon" icon="fa-solid fa-check"/>

                    </div>
                    <div class="text">
                        약관 전체 동의
                    </div>

                </label>
            </v-btn>
        </div>
        <form novalidate @submit.prevent="validate">
            <v-expansion-panels
                    v-model="usePannel"
                    :flat="true"
                    class="pannel-box"
                    v-if="agreeInfo.use.use=='yes'"
            >
                <v-expansion-panel style="border-radius: 10px;">
                    <v-expansion-panel-header class="pannel-header">
                        <div style="max-width:30px;overflow:hidden;">
                            <checkbox v-model="agreeUse"></checkbox>
                        </div>
                        <div class="agree-title">(필수) 이용약관</div>

                    </v-expansion-panel-header>
                    <v-expansion-panel-content class="pannel-body">
                        <div class="content" v-html="agreeInfo.use.content">

                        </div>
                    </v-expansion-panel-content>
                </v-expansion-panel>


            </v-expansion-panels>
            <v-expansion-panels
                    v-model="privacyPannel"
                    :flat="true"
                    class="pannel-box"
                    v-if="agreeInfo.privacy.use=='yes'"
            >
                <v-expansion-panel style="border-radius: 10px;">
                    <v-expansion-panel-header class="pannel-header">
                        <div style="max-width:30px;overflow:hidden;">
                            <checkbox v-model="privacyUse"></checkbox>
                        </div>
                        <div class="agree-title">(필수) 개인정보 처리방침</div>

                    </v-expansion-panel-header>
                    <v-expansion-panel-content class="pannel-body">
                        <div class="content" v-html="agreeInfo.privacy.content">
                        </div>
                    </v-expansion-panel-content>
                </v-expansion-panel>


            </v-expansion-panels>

            <v-expansion-panels
                    v-model="collectPannel"
                    :flat="true"
                    class="pannel-box"
                    v-if="agreeInfo.collect.use=='yes'"
            >
                <v-expansion-panel style="border-radius: 10px;">
                    <v-expansion-panel-header class="pannel-header">
                        <div style="max-width:30px;overflow:hidden;">
                            <checkbox v-model="collectUse"></checkbox>
                        </div>
                        <div class="agree-title">(필수) 개인정보 수집/동의항목</div>

                    </v-expansion-panel-header>
                    <v-expansion-panel-content class="pannel-body">
                        <div class="content" v-html="agreeInfo.collect.content">
                        </div>
                    </v-expansion-panel-content>
                </v-expansion-panel>


            </v-expansion-panels>
            <div class="button-row">
                <v-btn type="submit" rounded class="blackButton">
                    <span>완료</span>
                </v-btn>
            </div>
        </form>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_MEMBER_AGREE,
        API_URL_MEMBER_JOIN,

    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import checkbox from '@/components/riverflowCheckbox'

    export default {
        name: 'pcJoinAgree',
        components: {
            checkbox
        },
        data() {
            return {
                usePannel: false,
                privacyPannel: false,
                collectPannel: false,

                frm: {},
                agreeUse: false,
                privacyUse: false,
                collectUse: false,
                agreeInfo: {
                    use: {
                        use: 'no',
                        content: '',
                    },
                    privacy: {
                        use: 'no',
                        content: '',
                    },
                    collect: {
                        use: 'no',
                        content: '',
                    }
                },
                checkClass: '',
                serverFlag: false,
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.$router.push('/');
                return;
            }
            //this.getData();
        },
        methods: {
            getData() {
                const info = sessionStorage.getItem('tempMemberInfo')
                if (!info) {
                    let params = {
                        message: '잘못된 접근입니다',
                        doAction: () => {
                            this.$router.push('/')
                        }
                    };
                    this.$eventBus.$emit('modalOpen', params);
                }
                const frm = JSON.parse(sessionStorage.getItem('tempMemberInfo'));
                if (!frm?.uid) {
                    let params = {
                        message: '잘못된 접근입니다',
                        doAction: () => {
                            this.$router.push('/')
                        }
                    };
                    this.$eventBus.$emit('modalOpen', params);
                }
                this.frm = frm;
                console.log(this.frm);
                apiService.get(API_URL_GET_MEMBER_AGREE, {}, (res) => {
                    if (res.data) {
                        this.agreeInfo = res.data;
                    }
                })
            },
            allCheck() {
                if (this.checkClass == 'active') {
                    this.checkClass = ''
                    this.agreeUse = false
                    this.privacyUse = false
                    this.collectUse = false

                } else {
                    this.checkClass = 'active'
                    this.agreeUse = true
                    this.privacyUse = true
                    this.collectUse = true

                }
            },
            validate() {
                if (this.serverFlag) return;

                if (!this.agreeUse) {
                    let params = {
                        message: '이용약관에 동의하세요',
                    };
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                if (!this.privacyUse) {
                    let params = {
                        message: '개인정보 처리방침에 동의하세요',
                    };
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                if (!this.collectUse) {
                    let params = {
                        message: '개인정보 수집/동의항목에 동의하세요',
                    };
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }

                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_MEMBER_JOIN, this.frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let result = res.status;
                    let params = {}
                    if (result == 'success') {
                        sessionStorage.setItem('tempMemberInfo','');

                        AUTH.setSession(res.data)
                        params = {
                            message: '회원가입을 축하 합니다',
                            doAction: () => {
                                location.href = '/';
                            }
                        };

                    } else {
                        let msg = '';
                        switch (res.data) {
                            case 'doubleUid':
                                msg = '중복된 이메일 입니다'
                                break;
                            case 'wrongAuthNumber':
                                msg = '잘못된 인증번호 입니다'
                                break;
                        }
                        params = {
                            message: msg,
                        };
                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            }
        }

    }
</script>


<style lang="scss" scoped>
    .login-container {
        width: 970px;
        margin: 100px auto;

        .login-title {
            text-align: center;
            color: #000;
            font-size: 30px;
            font-weight: bold;
            margin-bottom: 45px;
        }

        .w-full {
            width: 100%;
            margin: auto;

            .input-box {
                width: 100%;
                margin: auto;
            }
        }

        .button-row {
            width: 424px;
            padding: 40px 0 10px 0;
            margin: auto;
        }

        .button-checkbox {
            width: 258px;
            margin: auto;
            padding: 20px 0 48px 0;
        }

        .sub-title {
            color: #232323;
            font-weight: 600;
            font-size: 16px;
            text-align: center;
        }

        .pannel-box {
            border: 1px solid #C9C9C9;
            box-sizing: border-box;
            border-radius: 10px;
            margin-bottom: 30px;
        }

        .pannel-header {
            max-height: 60px;
            height: 60px;
            display: flex;

            .agree-title {
                font-weight: 700;
                font-size: 16px;
                color: #000000;
            }
        }

        .pannel-body {
            border-top: 1px solid #C9C9C9;

            .content {
                padding-top: 15px;
                font-size: 14px;
                color: #4E4E4E;
            }

        }


        .custome-checked-box {
            width: 100%;
            display: flex;
            padding-left: 40px;
            cursor: pointer;

            .out-line {

                width: 24px;
                height: 24px;
                border-radius: 50%;
                background: #E0E0E7;
                color: #fff;
                position: relative;

                .virtual-checkbox {

                    cursor: pointer;
                    width: 100%;
                    height: 100%;
                    opacity: 0;

                }

                .checked-icon {
                    position: absolute;
                    top: 6px;
                    left: 6px;
                }

                &.active {
                    background: #000;
                }

            }

            .text {
                padding-left: 10px;
                padding-top: 2px;
                color: #000;
                font-weight: 600;
                font-size: 16px;

            }
        }
    }
</style>