<template>
    <div>
        <label class="custome-checked-box">
            <div :class="'out-line '+checkClass">
                <font-awesome-icon class="checked-icon"  icon="fa-solid fa-check"/>
                <input
                    class="virtual-checkbox"
                    type="checkbox"
                    v-bind:checked="checked"
                    v-on:change="$emit('change',$event.target.checked)"
                >
            </div>
            <div class="text" :style="textStyle" v-if="text">
                {{ text }}
            </div>

        </label>
    </div>
</template>
<script>


export default {
    model: {
        prop: 'checked',
        event: 'change'
    },
    props: {
        checked: Boolean,
        text: String,
        options:{},
    },
    watch: {
      checked(val) {
          if(val)this.checkClass = 'active'
          else this.checkClass = ''
      }
    },
    data() {
        return {
            checkClass:'',
            textStyle:{},
        };
    },
    created() {
        if(this.options && this.options.textStyle) {
            this.textStyle = this.options.textStyle;
        }
    },
    methods: {


    }

}
</script>

<style lang="scss" scoped>
.custome-checked-box {
    width:100%;
    display: flex;
    cursor: pointer;
    .out-line {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background: #E0E0E7;
        color: #fff;
        position: relative;
        .virtual-checkbox {

            cursor: pointer;
            width: 100%;
            height: 100%;
            opacity: 0;

        }

        .checked-icon {
            position: absolute;
            top: 6px;
            left: 6px;
        }
        &.active {
            background: #000;
        }

    }
    .text {

        padding-left:10px;
        color:#232323;
        font-weight: 600;
        font-size:16px;

    }
}
</style>